.square {
  width: 125px;
  height: 125px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border-radius: 44px;
  background: linear-gradient(#5011ff, #460a99);
}

.square-border {
  position: absolute !important;
  width: 100% !important;
  height: 100% !important;
}

.square-border::before {
  content: "";
  position: absolute;
  width: 100% !important;
  height: 100% !important;
  inset: 0px !important;
  background: url(./assets/box-border.svg) !important;
  background-size: cover !important;
  background-repeat: no-repeat !important;
  background-position: center !important;
  z-index: 100 !important;
}

.square.connected::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
  background: linear-gradient(#11ff88 5%, #460a99 100%);
  z-index: 10 !important;
  opacity: 0;
  -webkit-animation: fadeIn 1s forwards;
  animation: fadeIn 1s forwards;
  border-radius: 44px !important;
}

.square.failed::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
  background: linear-gradient(#fe1150 5%, #460a99 100%);
  z-index: 10 !important;
  opacity: 0;
  -webkit-animation: fadeIn 1s forwards;
  animation: fadeIn 1s forwards;
  border-radius: 44px !important;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.m-icon {
  position: absolute;
  width: 65% !important;
  height: 65% !important;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  opacity: 1;
  transition: opacity 0.7s !important;
  z-index: 10;
}

.m-icon-check {
  top: 53.5% !important;
}

.m-hide {
  opacity: 0;
  z-index: -1;
}

.square h1 {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  color: #fff;
  margin: 0;
  margin-top: 2.5px !important;
  font-size: 40px;
  font-weight: 800 !important;
}

@media (max-width: 992px) {
  .square h1 {
    font-size: 35px !important;
  }
}

.text-result > * {
  color: #fff !important;
  font-size: 24px !important;
  font-weight: 600 !important;
  margin-top: 10px !important;
  font-weight: 600 !important;
  direction: rtl !important;
}

@media (max-width: 992px) {
  .text-result > * {
    font-size: 16px !important;
    margin-top: 4px !important;
  }
}

.main-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* Text Animation */

.flip-number-container {
  font-family: "system-ui", sans-serif !important;
  font-weight: 700 !important;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  line-height: 48px !important;
  overflow: visible;
}

.flip-number-container > * {
  font-size: 40px;
  font-family: "system-ui", sans-serif !important;
  font-weight: 700 !important;
  line-height: 48px !important;
  color: #fff;
}
